.nav {
  background-image: url(../../assets/creamTexture.jpeg);
  height: 10vh;
  max-height: 70px;
  min-height: 50px;
  width: 100%;
}

.nav-container {
  display: flex;
  height: 100%;
  justify-content: space-between;
  margin: 0 auto 0 auto;
  max-width: 1600px;
  padding: 0;
  width: 100%;
}

.nav-about {
  align-items: center;
  display: flex;
  font-family: "Allerta Stencil", sans-serif;
  cursor: pointer;
  height: 100%;
  font-weight: 500;
  letter-spacing: 2px;
  font-size: 10px;
  text-align: left;
  width: 30%;
}
@media (min-width: 600px) {
  .nav-about {
    font-size: 13px;
  }
}
@media (min-width: 800px) {
  .nav-about {
    font-size: 18px;
  }
}
@media (min-width: 1000px) {
  .nav-about {
    font-size: 20px;
  }
}

.nav-about-title {
  margin-left: 5%;
}

.nav-img {
  cursor: pointer;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40%;
}

.nav-account-cart-container {
  display: flex;
  justify-content: right;
  align-items: center;
  margin: 0 0 0 0;
  width: 30%;
}

.nav-account {
  cursor: pointer;
  font-family: "Allerta Stencil", sans-serif;
  font-weight: 500;
  letter-spacing: 2px;
  margin-right: 5%;
  font-size: 12px;
}
@media (min-width: 600px) {
  .nav-account {
    font-size: 13px;
  }
}
@media (min-width: 800px) {
  .nav-account {
    font-size: 18px;
  }
}
@media (min-width: 1000px) {
  .nav-account {
    font-size: 20px;
  }
}

.about-page {
  visibility: hidden;
  height: 100vh;
  width: 100vw;
  background-color: black;
  justify-content: top;
  align-items: center;
  position: absolute;
  z-index: 2;
  opacity: 0.85;
  color: white;
  flex-direction: column;
  transition: visibility 0.3s, opacity 0.3s ease-in-out;
}

.about-page-active {
  visibility: visible;
  opacity: 0.8;
  cursor: pointer;
  text-align: center;
}

@-webkit-keyframes about {
  from {
    opacity: 0;
  }
  to {
    opacity: 0.85;
  }
}

@keyframes about {
  from {
    opacity: 0;
  }
  to {
    opacity: 0.85;
  }
}
@-webkit-keyframes aboutout {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
@keyframes aboutout {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
.about-title {
  font-size: 40px;
  margin-bottom: 70px;
  margin-top: 150px;
  text-align: center;
}

.about-info {
  font-size: 40px;
  text-align: center;
  line-height: 50px;
  font-weight: 400;
  margin-bottom: 70px;
}

.about-how-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.about-how-title {
  font-family: "Allerta Stencil";
  font-size: 30px;
  margin-bottom: 20px;
}

.about-how-info {
  font-family: "Barlow";
  width: 77%;
  text-align: center;
  line-height: 25px;
  font-size: 18px;
  font-weight: 100;
}/*# sourceMappingURL=nav.css.map */