.mbsmerch-container {
  background-color: rgb(255, 255, 255);
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  text-align: center;
  max-width: 1600px;
  margin: 0 auto 0 auto;
}

.mbsmerch-tiles-container {
  margin-bottom: 130px;
  display: flex;
}

.mbsmerch-tiles-box-container1 {
  width: 280px;
  height: 400px;
  border-radius: 10px;
  display: flex;
  justify-content: center;
}
@media (min-width: 425px) {
  .mbsmerch-tiles-box-container1 {
    width: 400px;
  }
}
@media (min-width: 654px) {
  .mbsmerch-tiles-box-container1 {
    width: 300px;
    height: 300px;
  }
}
@media (min-width: 1024px) {
  .mbsmerch-tiles-box-container1 {
    width: 400px;
    height: 400px;
  }
}
@media (min-width: 1440px) {
  .mbsmerch-tiles-box-container1 {
    width: 500px;
    height: 500px;
  }
}

.mbsmerch-tiles-box-container2 {
  width: 50%;
  background-color: white;
  height: 100%;
  margin-left: 1%;
  border-radius: 10px;
  display: none;
  flex-direction: column;
  justify-content: space-between;
}
@media (min-width: 654px) {
  .mbsmerch-tiles-box-container2 {
    display: flex;
    width: 180px;
    height: 300px;
  }
}
@media (min-width: 1024px) {
  .mbsmerch-tiles-box-container2 {
    width: 300px;
    height: 400px;
  }
}
@media (min-width: 1440px) {
  .mbsmerch-tiles-box-container2 {
    width: 300px;
    height: 500px;
  }
}

.mbsmerch-tiles-box-container3 {
  width: 24%;
  background-color: white;
  height: 100%;
  margin-left: 1%;
  border-radius: 10px;
  display: none;
  flex-direction: column;
  justify-content: space-between;
}
@media (min-width: 1024px) {
  .mbsmerch-tiles-box-container3 {
    display: flex;
    width: 300px;
    height: 400px;
  }
}
@media (min-width: 1440px) {
  .mbsmerch-tiles-box-container3 {
    width: 300px;
    height: 500px;
  }
}

.mbsmerch-box1-btn {
  width: 52%;
  height: 12%;
  background-color: rgb(0, 0, 0);
  color: rgb(230, 215, 176);
  font-size: 130%;
  border-radius: 10px;
  border-width: 0px;
  cursor: pointer;
}

.mbsmerch-box1 {
  width: 100%;
  background-color: rgba(230, 215, 176, 0.35);
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
}

.mbsmerch-box2 {
  width: 100%;
  background-color: rgb(230, 215, 176);
  opacity: 0.35;
  height: 56%;
  border-radius: 10px;
}

.mbsmerch-box3 {
  width: 100%;
  background-color: #e6d7b0;
  opacity: 0.35;
  height: 42%;
  border-radius: 10px;
}

.mbsmerch-box4 {
  width: 100%;
  height: 35%;
  background-color: #e6d7b0;
  opacity: 0.35;
  border-radius: 10px;
}

.mbsmerch-box5 {
  width: 100%;
  height: 63%;
