  opacity: 0.35;
  border-radius: 10px;
}

.mbsmerch-title {
  margin-bottom: 90px;
  font-size: 30px;
  letter-spacing: 2px;
  font-family: "Allerta Stencil", sans-serif;
  margin-top: 60px;
}

.mbsmerch-box1-btn {
  width: 60%;
  height: 8%;
  border-radius: 5px;
  background-image: url("../../assets/footerTexture.jpeg");
  color: white;
  letter-spacing: 2px;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.25);
  border: none;
  border-width: 0px;
  font-family: "Allerta Stencil", sans-serif;
  font-size: 15px;
}
@media (min-width: 324px) {
  .mbsmerch-box1-btn {
    font-size: 20px;
  }
